<template>
  <div id="app">
    <router-view />
    <foot
      v-if="$route.path!=='/login'&&$route.path!=='/create'&&$route.path!=='/recover'"
    ></foot>
    <div v-if="$route.path=='/me'||$route.path=='/home'" class="floatMenu">
      <div class="box flex flexdc flexcenter saoyisao" @click="floatMenu('saoyisao')">
        <span class="iconfont icon-scan"></span>
        <span class="text">扫一扫</span>
      </div>
      <div class="box flex flexdc flexcenter shouqian" @click="floatMenu('shouqian')">
        <span class="iconfont icon-shouqian"></span>
        <span class="text">收钱</span>
      </div>
      <div class="box flex flexdc flexcenter shangjia" @click="floatMenu('shangjia')">
        <span class="iconfont icon-shangjia"></span>
        <span class="text">商家</span>
      </div>
      <div class="box flex flexdc flexcenter daka" @click="floatMenu('daka')">
        <span class="iconfont icon-daqia1"></span>
        <span class="text">打卡</span>
      </div>
    </div>
    <!-- @click="closeWindow" -->
    <div v-if="maskwindow" class="maskwindow" @touchmove.prevent></div>
    <div v-if="selectMenu=='shouqian'" class="shoukuan popupWindow pre" @click="closeSelectcoin">
      <div class="title">收款</div>
      <div class="qrcode">
        <div class="tip">扫一扫，向我付钱</div>
        <img src="../static/qrcode.png" />
      </div>
      <div class="setinfo">
        <div class="jine">
          <input placeholder="输入收款金额 (CNY)" />
        </div>
        <div class="selectBox selectBox1 flex flexjcsb" @click="showSelectcoin('youhui')">
          <div>优惠设置</div>
          <div class="coinType">
            <span>{{setval.youhui}}</span>
            <span class="iconfont icon-arrow-sl"></span>
          </div>
        </div>
        <div class="selectBox flex flexjcsb" @click="showSelectcoin('cointype')">
          <div>{{setval.coinType}}</div>
          <div class="coinType">
            <span class="iconfont icon-arrow-sl"></span>
          </div>
        </div>
        <div class="selectBox flex flexjcsb" @click="showSelectcoin('diya')">
          <div>抵押</div>
          <div class="coinType">
            <span>{{setval.diya}}</span>
            <span class="iconfont icon-arrow-sl"></span>
          </div>
        </div>
      </div>
      <span class="iconfont icon-guanbi" @click="closeWindow"></span>
    </div>

    <div v-if="selectCoin" class="selectCoin">
      <div class="defineCancel flex flexjcsb">
        <div class="cancel flexcenter" @click="closeSelectcoin">取消</div>
        <div class="define flexcenter" @click="sureSelectcoin">确定</div>
      </div>
      <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
    </div>
    <div class="isInjectedFalse" v-if="isInjected==false">
      <div class="modelView" @click="closeModel()"></div>
      <div class="appinstall flex flexjcsb flexac">
        <div class="flex flexac grow">
          <div class="applogo">
            <img src="../static/eth_plug.png" />
          </div>
          <div class="installInfo">
            <div class="flex flexjcfs">MetaMask</div>
            <div
              class="flex flexjcfs conText"
            >DAPP requires MetaMask plug-in environment. Please install this plug-in or open it with ETH DAPP browser. ImToken is recommended</div>
          </div>
        </div>
        <div class="anzhuang flexcenter" @click="installMetaMask()">安装</div>
      </div>
    </div>
    <div v-if="isRegist">
      <div class="modelView" @click="closeModel()"></div>
      <div class="popupWindow shengjiWindow">
        <div class="title">
          <div class="num">还没有注册</div>
        </div>
        <div class="con">
          <div class="info">
            识别您当前的级别为
            <span>[非节点]</span>, 您可升级为 "消费节点" , 将从 "种子余额" 中扣除相应的ET, 若不够则从钱包账户中扣除, 情再下方确认操作!
          </div>
          <div class="msg">
            升级至
            <span class="nodeType">消费节点</span>
            <span class="num">10 ET</span>
          </div>
        </div>
        <div class="btnul flex flexjcsb">
          <div class="cancel flexcenter" @click="closeWindow">取消</div>
          <div class="define flexcenter submitBtn">下一步</div>
        </div>
      </div>
    </div>
  </div>
</template>



<script>
import { mapState } from "vuex";
import { ethers } from "ethers";
import Clipboard from "clipboard";
import { NETWORKS } from "./factory/contract/Networks.js";
import Providers from "./factory/contract/Providers.js";
import Constants from "./factory/contract/Constants.js";
const random = require("string-random");
const axios = require("axios");
import foot from "./components/foot";
import { Indicator } from 'mint-ui';
import { MessageBox } from "mint-ui";

export default {
  name: "dapp-component",
  computed: {
    ...mapState({
      network: state => NETWORKS[state.web3.networkId],
      coinbase: state => state.web3.coinbase,
      balance: state => state.web3.balance,
      isInjected: state => state.web3.isInjected
    })
  },
  watch: {
    '$store.state.player.joinGame':function(newVal,oldVal){
      if(newVal == true){
        // this.joinGame();
      }
      console.log('$store.state.player.joinGame  ' + newVal);
      console.log('$store.state.player.joinGame  ' + oldVal);
      console.log('$store.state.player.joinGame  ' + this.$store.state.player.joinGame);
    }
  },
  async beforeCreate() {},
  async created() {
    this.$store.state.player.beInvitedCode =  this.$route.query.code; 
  },
  async beforeDestroy() {
    clearInterval(this.time_contractInfo);
  },
  async mounted() {
    // for(let i=0;i<26;i++){
    //   console.log(" " + i,mnemonic)

    //   //拿到生成的钱包信息
    //   var wallet = ethers.Wallet.createRandom();
      
    //   //获取助记词
    //   var mnemonic = wallet.mnemonic;
    //   console.log("钱包助记词：" + i,mnemonic)
      
    //   //获取path
    //   var path = wallet.path;
    //   console.log("钱包path："+ i,path)
      
    //   //获取钱包的私钥
    //   var privateKey = wallet.privateKey;
    //   console.log("钱包私钥："+ i,privateKey)
      
    //   //获取钱包地址
    //   var address = wallet.address;
    //   console.log("钱包地址："+ i,address)

    //   console.log(" " + i,mnemonic)

    // }

    // Indicator.open({
    //   text: '加载合约中...',
    //   spinnerType: 'fading-circle'
    // });
    try {
      console.log(
        "registerEthers Action dispatched from casino-dapp.vue  beforeCreate"
      );
      if (!Providers.isMetaMaskProvider()){
        this.$store.state.web3.isInjected = false;
      }
      await this.$store.dispatch("registerEthers");
      console.log("dispatching getContractInstance mounted");
      await this.$store.dispatch("getContractInstance");
      if (Providers.isMetaMaskProvider()) {
        console.log("isMetaMaskProvider  ");
        this.time_contractInfo = setInterval(() => {
          this.getContractInfo();
        }, this.interval_contractInfo);
      }
    }
    finally {
        Indicator.close();
    }
  },
  data() {
    return {
      interval_contractInfo: 2000,
      slots: [
        {
          flex: 1,
          values: [
            "2015-01",
            "2015-02",
            "2015-03",
            "2015-04",
            "2015-05",
            "2015-06"
          ],
          className: "slot1",
          textAlign: "right"
        }
      ],
      selectMenu: "",
      maskwindow: false,
      selectCoin: false,
      selectchange: "",
      selectType: "",
      setval: {
        youhui: "1%",
        coinType: "USDT(泰达币)",
        diya: "关"
      },
      isMetamask: true,
      isRegist: false
    };
  },
  methods: {
    getContractInfo: function() {
      if (this.$store.state.etherNetUtilContractInstance == null) return;
      this.getPriceInfo();
      this.getEtherNetnfo();
      this.getGameInfo();
      this.getBalanceInfo();
      if (this.coinbase != null) this.getPlayerInfoPlyAdd();
      this.interval_contractInfo = 2000;
    },
    getGameInfo: function() {
      let _this = this;
      this.$store.state
        .etherNetContractInstance()
        .getGameInfo()
        .then(info => {
          console.log(" getGameInfo  " + info);
          _this.$store.state.gameInfo.pid = info[0];
          _this.$store.state.etherNetUtilInfo.usdtTotal = ethers.utils.formatUnits(
            "" + info[1],
            "Mwei"
          );
          _this.$store.state.etherNetUtilInfo.ethTotal = ethers.utils.formatUnits(
            "" + info[2],
            "Ether"
          );
        });
    },
    getPlayerInfoPlyAdd: function() {
      console.log(" getPlayerInfoPlyAdd  ");
      let _this = this;
      this.$store.state
        .etherNetContractInstance()
        .getPlayerInfoPlyAdd(this.coinbase)
        .then(info => {
          console.log(" getPlayerInfoPlyAdd  " + info);
          _this.$store.state.player.plyAddr = info[0];
          _this.$store.state.player.etsFreezeAmount = ethers.utils.formatUnits(
            "" + info[1],
            "Mwei"
          );
          _this.$store.state.player.etsFreeAmount = ethers.utils.formatUnits(
            "" + info[2],
            "Mwei"
          );
          _this.$store.state.player.etFreezeAmount = ethers.utils.formatUnits(
            "" + info[3],
            "Mwei"
          );
          _this.$store.state.player.etFreeAmount = ethers.utils.formatUnits(
            "" + info[4],
            "Mwei"
          );
          _this.$store.state.player.etSeedAmount = ethers.utils.formatUnits(
            "" + info[5],
            "Mwei"
          );
          _this.$store.state.player.etPoolUnit = info[6];
          _this.$store.state.player.level = info[7];
          _this.$store.state.player.levelName = _this.glabal.getLevelName("" + info[7]);
          _this.$store.state.player.upLevel = parseInt(info[7]) + 1;
          _this.$store.state.player.upLevelName = _this.glabal.getLevelName(parseInt(info[7]) + 1);
          _this.$store.state.player.upLevelNubmer = _this.glabal.getLevelNubmer(parseInt(info[7]) + 1);
          _this.$store.state.player.regTime = info[8];
          _this.$store.state.player.parallelTime = info[9];
          _this.$store.state.player.inviteCode = info[10];
          _this.$store.state.player.beInvitedCode = _this.glabal.isNull(_this.$store.state.player.beInvitedCode)?info[11]:_this.$store.state.player.beInvitedCode;
          _this.$store.state.player.inviteUrl =   "https://www.ethernetwork.org/#/my/?code=" +   _this.$store.state.player.inviteCode; 
          if(_this.$store.state.player.parallelTime>0){
              _this.$store.state.player.parallelDay = parseInt((21 - this.glabal.getDateDiff(new Date().getTime()/1000,_this.$store.state.player.parallelTime)));
          }
          console.log(" _this.$store.state.player.parallelDay  " + _this.$store.state.player.parallelDay);

        });
    },
    getEtherNetnfo: function() {
      console.log(" getEtherNetnfo  ");
      let _this = this;
      this.$store.state
        .etherNetUtilContractInstance()
        .getEtherNetUtilInfo()
        .then(info => {
          console.log(" getEtherNetUtilInfo  " + info);
          _this.$store.state.etherNetUtilInfo.etDifficulty = ethers.utils.formatUnits(
            "" + info[0],
            "Mwei"
          );
          _this.$store.state.etherNetUtilInfo.inflationRate = info[1]/1000;
          _this.$store.state.etherNetUtilInfo.parallelRate = info[2];
        });
    },
    getPriceInfo() {
      console.log(" getPriceInfo  ");
      let _this = this;
      this.$store.state
        .etherNetUtilContractInstance()
        .getPriceInfo()
        .then(info => {
          console.log(" getPriceInfo  " + info);
          _this.$store.state.priceInfo.usdtPrice = info[0] / 1000;
          _this.$store.state.priceInfo.ethPrice = (info[1] / 1000) * _this.$store.state.priceInfo.usdtPrice;
          _this.$store.state.priceInfo.etsPrice = info[2] / 1000;
          _this.$store.state.priceInfo.etPrice = info[3] / 1000;
        });
    },
    getBalanceInfo() {
      console.log(" getbalanceInfo  ");
      if(this.coinbase==0) return;
      let _this = this;
      this.$store.state
      .usdtContractInstance()
      .balanceOf(this.coinbase)
      .then(info => {
        console.log(" getbalanceInfo  " + info);
        _this.$store.state.balanceInfo.usdtBalance = ethers.utils.formatUnits( "" + info, "Mwei");
      });
      // this.$store.state
      // .etsContractInstance()
      // .balanceOf(this.coinbase)
      // .then(info => {
      //   console.log(" getbalanceInfo  " + info);
      //   _this.$store.state.balanceInfo.etsBalance = ethers.utils.formatUnits( "" + info, "Mwei");
      // });
    },
    // joinGame() {
    //   let _this = this;
    //   MessageBox({
    //     $type: 'prompt',
    //     title: "加入游戏",
    //     message: "你还未加入游戏<br>合约交易完成后再进行相应操作",
    //     confirmButtonText: "确定",
    //     cancelButtonText: "取消",
    //     closeOnClickModal: false,   
    //     showCancelButton: true,    
    //     inputErrorMessage: "请输入正确的提现数量",
    //     showInput: false
    //   }).then(({ value, action }) => {
    //     if (action == "confirm") {
         
    //     }
    //     else{
          
    //     }
    //   }).catch(() => { });
    // },
    installMetaMask() {
       let _this = this;
      _this.glabal.installMetaMask();
    },
    closeModel() {
      this.$store.state.web3.isInjected = true;
      this.isMetamask = false;
      this.maskwindow = false;
      this.isRegist = false;
    },
    closeSelectcoin() {
      this.selectCoin = false;
    },
    sureSelectcoin() {
      if (this.selectType == "youhui") {
        this.setval.youhui = this.selectchange;
      } else if (this.selectType == "cointype") {
        this.setval.coinType = this.selectchange;
      } else if (this.selectType == "diya") {
        this.setval.diya = this.selectchange;
      }
      this.selectCoin = false;
    },
    onValuesChange(picker, values) {
      this.selectchange = values[0];
    },
    showSelectcoin(type) {
      event.stopPropagation();
      this.selectCoin = true;
      this.selectType = type;
    },
    closeWindow() {
      this.maskwindow = false;
      this.selectCoin = false;
      this.isMetamask = false;
      this.isRegist = false;
      this.selectMenu = "";
    },
    floatMenu(type) {
      this.maskwindow = true;
      this.selectMenu = type;
    }
  },
  components: {
    foot
  }
};
</script>
<style rel="stylesheet/scss" lang="scss">
.shoukuan {
  height: 430px;
  .icon-guanbi {
    position: absolute;
    top: 16px;
    right: 12px;
    font-size: 14px;
  }
  .qrcode {
    .tip {
      color: #5c5c5c;
    }
  }
  .setinfo {
    .selectBox {
      width: 70%;
      margin: 0 auto;
      border-top: 1px solid #f5f5f5;
      padding: 13px 0;
    }
    .selectBox1 {
      border-top: 1px solid #999;
    }
    .jine {
      width: 55%;
      margin: 0 auto;
      input {
        width: 100%;
        height: 35px;
        border: none;
        outline: none;
        //  text-indent: 20px;
        text-align: center;
        font-size: 15px;
      }
    }
  }
  .title {
    padding: 25px 0 5px;
    font-weight: 600;
    font-size: 17px;
  }
  .qrcode {
    width: 56%;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background-color: #f5f5f5;
  height: 100vh;
}
.floatMenu {
  position: fixed;
  right: 15px;
  bottom: 90px;
  .box {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    font-size: 12px;
    color: #fff;
    margin-top: 20px;
  }
  .saoyisao {
    background-color: #f54b3c;
  }
  .shouqian {
    background-color: #3377ff;
  }
  .shangjia {
    background-color: #46cdea;
  }
  .daka {
    background-color: #fab905;
  }
}

.isInjectedFalse .appinstall {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 6666666;
  height: 88px;
  padding: 16px 1rem;
  background-color: #ff682f;
  color: #fff;
}

.modelView {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  background: #000;
  z-index: 22;
}
.anzhuang {
  min-width: 43px;
  min-height: 27px;
  border: 1px solid #f0f0f0;
}
.applogo img {
  max-width: 33px;
  max-height: 33px;
}
.conText {
  text-align: left;
  font-size: 12px;
}
.mint-msgbox {
  height: 270px !important;
  width: 80%!important;
  border-radius: 12px !important;
}
.mint-msgbox-header {
  background-image: url(../static/tctitle.png)!important;
  background-size: 100% 100%!important;
  margin-top: 0 !important;
  color: #fff!important;
  padding: 25px 0!important;
  .mint-msgbox-title {
    text-align: left!important;
    padding-left: 20px!important;
    color: #fff!important;
  }
}
.mint-msgbox-content {
  border-bottom: none!important;
}
.mint-msgbox-btns {
  position: absolute!important;
  left: 0!important;
  bottom: 25px !important;
  width: 84% !important;
  right: 0!important;
  margin: auto!important;
  justify-content: space-between!important;
}
.mint-msgbox-btn {
  border-right: none!important;
  max-width: 127px!important;
  height: 36px!important;
  border-color: var(--btnbr)!important;
  border-radius: 25px!important;
  border: 1px solid #bdbdbd!important;
  -webkit-box-sizing: border-box!important;
  box-sizing: border-box!important;
}

.mint-msgbox-confirm {
  border-radius: 30px!important;
  background-color: var(--basetext)!important;
  color: #fff!important;
  background: url("../static/btnBg.png")!important;
  background-size: 100% 100%!important;
  border: none!important;
}
</style>



