import { ethers } from 'ethers';

var Wallet = {};
var Tool = {};

Wallet.generateMnemonic = function() {
    return ethers.Wallet.createRandom().mnemonic;
}

Wallet.createWalletFromMnemonic = function(mnemonicWord) {
    let walletInfo =   null;
    let mnemonicWallet =  null;
    try {
        mnemonicWallet = ethers.Wallet.fromMnemonic(mnemonicWord);
        walletInfo = {
            mnemonicWord: mnemonicWord,
            privateKey: mnemonicWallet.privateKey,
            address: mnemonicWallet.address,
            path: mnemonicWallet.path,
            avatar: Math.floor(Math.random() * 4)
        };
    } catch (error) {
        console.log(error)
    }
    return walletInfo
}

Wallet.createWalletFromPrv = function(privateKey) {
    let walletInfo =   null;
    let privateKeyWallet = null;
    try {
        privateKeyWallet = new ethers.Wallet(privateKey);
        walletInfo = {
            mnemonicWord: "",
            privateKey: privateKeyWallet.privateKey,
            address: privateKeyWallet.address,
            path: privateKeyWallet.path,
            avatar: Math.floor(Math.random() * 3)
        };
    } catch (error) {
        console.log(error)
    }
    return walletInfo
}

Tool.getFullNum = function(num) {
    if (isNaN(num)) {
        return num
    };
    var str = '' + num;
    if (!/e/i.test(str)) {
        return num;
    };
    return (num).toFixed(18).replace(/\.?0+$/, "");
}

Tool.accMul = function(arg1, arg2) {
    var m = 0,
        s1 = arg1.toString(),
        s2 = arg2.toString();
    try { m += s1.split(".")[1].length } catch (e) {}
    try { m += s2.split(".")[1].length } catch (e) {}
    return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
}

Tool.Xreplace = function Xreplace(str, length, reversed) {
    var re = new RegExp("\\w{1," + length + "}", "g");
    var ma = str.match(re);
    if (reversed) ma.reverse();
    for (let i = 0; i < ma.length; i++) {
        ma[i] = parseInt('0x' + ma[i]);
    }
    return ma;
}

export default {
    Wallet,
    Tool
}


