import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            redirect: '/my',
        },
        {
            path: '/login',
            name: "login",
            meta: {
                title: "登录",
                requireAuth: true,
            },
            component: () =>
                import ('@/views/wallet/index'),
            hidden: true
        },
        {
            path: '/recover',
            name: "recover",
            meta: {
                title: "恢复账号",
                requireAuth: true,
            },
            component: () =>
                import ('@/views/wallet/recover'),
            hidden: true
        },
        {
            path: '/create',
            name: "create",
            meta: {
                title: "创建帐号",
                requireAuth: true,
            },
            component: () =>
                import ('@/views/wallet/create'),
            hidden: true
        },

        {
            path: '/my',
            name: "my",
            meta: {
                title: "用户中心",
                requireAuth: true,
            },
            component: () =>
                import ('@/views/me'),
            hidden: true
        },
        {
            path: '/home',
            name: "home",
            meta: {
                title: "首页",
                requireAuth: true,
            },
            component: () =>
                import ('@/views/home'),
            hidden: true
        },
        {
            path: '/trade',
            name: "trade",
            meta: {
                title: "交易",
                requireAuth: true,
            },
            component: () =>
                import ('@/views/trade'),
            hidden: true
        },
        {
            path: '/progress',
            name: "progress",
            meta: {
                title: "用户中心",
                requireAuth: true,
            },
            component: () =>
                import ('../components/progress'),
            hidden: true
        }
    ]
})


