import BaseContract from './BaseContract.js';
import EtherNetABI from "./abi/EtherNetABI.json";
import EtherNetUtilABI from "./abi/EtherNetUtilABI.json";
import ERCABI from "./abi/ERCABI.json";
import Constants from "./Constants.js"

let contractResolve;
let getContract = new Promise(function (resolve, reject) {
  contractResolve = resolve;
});

export const initContract = () => {
	let etherNetContract = new BaseContract().createContractInstance(Constants.EtherNetContractAddress, EtherNetABI);
	let etherNetUtilContract = new BaseContract().createContractInstance(Constants.EtherNetUtilContractAddress, EtherNetUtilABI);
	let usdtContract = new BaseContract().createContractInstance(Constants.USDTContractAddress, ERCABI );
	let etsContract = new BaseContract().createContractInstance(Constants.ETSContractAddress, ERCABI );
	let etContract = new BaseContract().createContractInstance(Constants.ETContractAddress, ERCABI );

	if ((etherNetContract == null || typeof etherNetContract !== 'undefined')&&(etherNetUtilContract == null || typeof etherNetUtilContract !== 'undefined')&&(usdtContract == null 
		|| typeof usdtContract !== 'undefined')&&(etsContract == null || typeof etsContract !== 'undefined')&&(etContract == null || typeof etContract !== 'undefined')) {
		contractResolve([etherNetContract,etherNetUtilContract,usdtContract,etsContract,etContract] )
	    console.log( 'create contract success ' );
	}else{
		console.log( 'create contract fail  ' );
	}
};

export default getContract



