function getLevelName(value) {
    let name  = "";
    switch (parseInt(value)) {
      case 0:
          name = "自由";
          break;
      case 1:
          name = "生产";
          break;
      case 2:
          name = "商家";
          break;
      case 3:
          name = "见证";
          break;
      case 4:
          name = "布道";
          break;
      case 5:
          name = "超级";
          break;
      case 6:
          name = "创世";
          break;
      case 7:
          name = "大神";
          break; 
      default:
          name = "自由";
    } 
    return name;
}

function getLevelNubmer(value) {
    let  nubmer  = 0;
    switch (parseInt(value)) {
      case 0:
          nubmer = 0;
          break;
      case 1:
          nubmer = 10;
          break;
      case 2:
          nubmer = 100;
          break;
      case 3:
          nubmer = 1000;
          break;
      case 4:
          nubmer = 10000;
          break;
      case 5:
          nubmer = 100000;
          break;
      case 6:
          nubmer = 1000000;
          break;
      case 7:
          nubmer = 10000000;
          break; 
      default:
          nubmer = 0;
    } 
    return nubmer;
}

function isNull( str ){
    if ( str==null || str == ""  ) return true;
    var regu = "^[ ]+$";
    var re = new RegExp(regu);
    return re.test(str);
}

function installMetaMask() {
    window.location.href = "https://metamask.io/";
}

function isJoinGame(obj) {
    console.log(obj);
    console.log("aaaaaaaaa" + isNotNull(obj));
    console.log("aaaaaaaaa" + obj.regTime );
    console.log("aaaaaaaaa" + obj.plyAddr);
    console.log("aaaaaaaaa" + isNull(obj.plyAddr));
    if(obj.regTime<=0||obj.plyAddr =="0x0000000000000000000000000000000000000000"){
        console.log("isJoinGame ----- false" );
        return false;
    } else{
        console.log("isJoinGame +++++++ true" );
        return true;
    }
}

function isNotNull(obj){
    if(typeof obj==='undefined'){//先判断类型
        return false;
    }else if(obj==null){
        return false;
    }else if(obj==''){
        return false;
    }
    return true;
}

function toDecimal(x) { 
    var f = parseFloat(x); 
    if (isNaN(f)) { 
    return false; 
    } 
    var f = Math.round(x*100)/100; 
    var s = f.toString(); 
    var rs = s.indexOf('.'); 
    if (rs < 0) { 
    rs = s.length; 
    s += '.'; 
    } 
    while (s.length <= rs + 2) { 
    s += '0'; 
    } 
    return s; 
} 

function getDateDiff(startTime,endTime) 
{ 
    var days = (startTime - endTime)/(60*60*24);    
    return parseInt(days);  
}

export default {
  getLevelName,
  getLevelNubmer,
  isNull,
  installMetaMask,
  toDecimal,
  getDateDiff,
  isJoinGame,
  isNotNull
}



