<template>
  <div class="foot navList bgfff flex">
    <div class="li flexcenter flexdc flex1" :class="($route.path).indexOf('/home')>0?'navActive':''" @click="goNav('home')">
      <img class="icon" v-if="($route.path).indexOf('home')<=0" src="../../../static/shouye1@2x.png" />
      <img class="icon" v-else src="../../../static/shouye2@2x.png" />
      <div class="name">首页</div>
    </div>
    <div class="li flexcenter flexdc flex1" :class="($route.path).indexOf('/trade')>0?'navActive':''" @click="goNav('trade')">
      <img class="icon" v-if="($route.path).indexOf('trade')<=0" src="../../../static/duihuan1@2x.png" />
      <img class="icon" v-else src="../../../static/duihuan2@2x.png" />
      <div class="name">交易</div>
    </div>
    <div class="li flexcenter flexdc flex1" :class="($route.path).indexOf('/my')>0?'navActive':''" @click="goNav('my')">
      <img class="icon" v-if="($route.path).indexOf('my')<=0" src="../../../static/wode1@2x.png" />
      <img class="icon" v-else src="../../../static/wode2@2x.png" />
      <div class="name">我的</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkTab: "tab1"
    };
  },
  methods: {
    goNav(type) {
        this.$router.push({
            path: "/"+type
          });
      // switch (type) {
      //   case "home":
      //     this.$router.push({
      //       path: "/home"
      //     });
      //      break;
      //   case "trade":
      //     this.$router.push({
      //       path: "/trade"
      //     });
      //      break;
      //      case "myindex":
      //     this.$router.push({
      //       path: "/myindex"
      //     });
      //      break;
      // }
    }
  },
  components: {}
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.navList {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
  border-top: 1px solid #e5e6eb;
  padding: 5px 0 0 0;
  .li {
    font-size: 13px;
    .icon {
      width: 24px;
    }
  }
}
</style>


