import Vue from 'vue';
import Vuex from 'vuex';
import { ethers } from 'ethers';
import localforage from 'localforage';
import state from './state'
Vue.use(Vuex);
import ethersInstance from '../factory/contract/EthersInstance'
import pollEthers from '../factory/contract/PollEthers'
import contractInstance, { initContract } from '../factory/contract/ContractInstance'
import Providers from '../factory/contract/Providers.js';

export const store = new Vuex.Store({
    state,
    mutations: {
        pollEthersInstance(state, payload) {
            //console.log('pollEthersInstance mutation being executed', payload)
            state.web3.coinbase = payload.coinbase
            state.web3.balance = ethers.utils.formatEther('' + payload.balance)
        },
        registerEthersInstance(state, payload) {
            console.log('registerEthersInstance  :', payload)
            let result = payload
            state.coinbase = result.coinbase
            state.networkId = result.networkId
            state.balance = ethers.utils.formatEther('' + result.balance)
            state.isInjected = result.isInjected
            state.web3Instance = result.web3
            pollEthers()
        },
        registerContractInstance(state, payload) {
            console.log('registerContractInstance : ', payload)
            state.etherNetContractInstance = () => payload[0]
            state.etherNetUtilContractInstance = () => payload[1]
            state.usdtContractInstance = () => payload[2]
            state.etsContractInstance = () => payload[3]
            state.etContractInstance = () => payload[4]
        },
        isFreshWallet(state, data) {
            state.isFreshWallet = data
        },
        setWalletDB(state, data) {
            state.walletDB.current = data.current,
            state.walletDB.addresses = data.addresses,
            state.walletDB.accounts = data.accounts
        },
        setIsRefreshShowMsg(state, data) {
            state.walletDB.isRefreshShowMsg = data;
        },
        setUpdataStatus(state, data) {
            state.walletDB.updataStatus = data
        },       
    },

    actions: {
        pollEthers({ commit }, payload) {
            //console.log('pollEthers action being executed')
            if (!Providers.isMetaMaskProvider()) return;
            commit('pollEthersInstance', payload)
        },
        registerEthers({ commit }) {
            console.log('registerEthers Action being executed')
            if (!Providers.isMetaMaskProvider()) return
            return ethersInstance().then(result => {
                console.log('committing result to registerEthersInstance mutation')
                commit('registerEthersInstance', result)
            }).catch(e => {
                console.log('error in action registerEthers', e)
            })
        },
        getContractInstance({ commit }) {
            if (!Providers.isMetaMaskProvider()) return;
            initContract();
            return contractInstance.then(result => {
                commit('registerContractInstance', result)
            }).catch(e => console.log(e))
        },
        saveWalletDB({dispatch, commit, state}, data) {
            commit('setWalletDB', {
                current: data.current,
                addresses: data.addresses,
                accounts: data.accounts
            })
            return Promise.resolve(
                localforage.setItem("walletDB", data).then( r => {
                    return Promise.resolve(true)
                })
            )
        },
    }
});
export default store



